@import "variables";

.contactSection {
	position: relative;
	background-size: 115%;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(255, 255, 255, .85);
	}
}

.contactForm {
	max-width: 600px;
	width: 100%;
	margin: 50px auto 60px;
	box-shadow: 0px 0px 16px 1px rgba(0,0,0,0.25);
	padding: 30px 20px 40px;
	text-align: center;
	background-color: $default;
	fieldset {
		display: flex;
	}
	label {
		text-align: left;
	}
}

.textField {
	border: 1px solid $main;
	width: 100%;
	margin: 5px 0 20px;
	padding: 10px 5px;
	@include trans;
	&:focus {
		box-shadow: 0px 0px 5px 0px rgba(33,150,243,0.75);
		outline: none;
	}
}

.send {
	margin: 20px auto;
}


@media only screen and (max-width: 767px) {
	.contactForm {
		margin: 25px auto;
	}
}