@import "variables";

.worksSliderWrap {
	max-width: 720px;
	width: 100%;
	margin: 0 auto;
	padding: 50px 0;
}

.worksSlider {
	position: relative;
}

.workSlide {
	width: 100%;
	height: 560px !important;
	display: flex;
	box-shadow: 0px 0px 16px 1px rgba(0,0,0,0.25);
	overflow: hidden;
	background-color: #fff;
	&:hover {
		img {
			transform: scale(1.2);
        	transition-duration: 5s;
		}
    }
	img {
		transform: scale(1);
    	transition-property: transform;
    	transition-duration: .3s;
    	transition-timing-function: linear;
	}
}

.slideLeft,
.slideRight {
	width: 50%;
}

.slideLeft {
	overflow: hidden;
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, .0);
		z-index: 1;
		@include trans;
	}
	&:hover {
		&:before {
			background-color: rgba(0, 0, 0, .5);
		}
		.slideLink {
			opacity: 1;
		}
	}
}

.slideRight {
	padding: 50px 30px;
	@include trans;
	p {
		margin: 0 0 20px;
	}
}

.slideLink {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 2;
	opacity: 0;
	@include trans;
}

.slideHeading {
	font-size: 30px;
	margin: 0 0 20px;
}

@media only screen and (max-width: 991px) {
	.worksSliderWrap {
		max-width: 600px;
		padding: 0;
	}
	.workSlide {
		height: 450px !important;
	}
	.slideRight {
		padding: 15px;
		h6, p {
			font-size: 14px;
		}
	}
	.slideHeading {
		font-size: 24px;
	}
}

@media only screen and (max-width: 767px) {
	.slideLink {
		min-width: 130px;
	}
}

@media only screen and (max-width: 480px) {
	.slideHeading {
		font-size: 20px;
	}
	.slideRight {
		h6, p {
			font-size: 13px;
		}
	}
	.workSlide {
		flex-direction: column;
		height: 650px !important;
	}
	.slideLeft,
	.slideRight {
		width: 100%;
		height: 50%;
	}
}

