@import "variables";
.pageHeader {
	min-height: 100vh;
	padding: 60px 0 40px;
	position: relative;
	background-attachment: fixed;
	display: flex;
	align-items: center;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, .4);
		z-index: 1;
	}
}
.content {
	display: grid;
	grid-template-columns: 50% 50%;
	grid-template-rows: 33% 33% 33%;
	&__inner {
		position: relative;
		z-index: 1;
		grid-row-start: 2;
		color: $default;
	}
}

.mainHeading {
	font-size: 40px;
	margin: 0 0 40px;
}
.descrText {
	font-size: 18px;
	letter-spacing: 0.9px;
}

.btnDown {
	position: absolute;
	display: block;
	cursor: pointer;
	bottom: 60px;
	right: 60px;
	width: 60px;
	height: 60px;
	background-image: url("../img/style/svg/down-chevron.svg");
	background-size: 40px;
	background-repeat: no-repeat;
	z-index: 1;
	@include trans;
	animation: down 2s infinite ease-in-out;
	&:hover {
		background-image: url("../img/style/svg/down-chevron-hover.svg");
		animation-play-state: paused;
		&:before {
			transform: perspective(200px) rotateY(0deg)
		}
	}
	&:before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border: 2px solid $main;
		transform: perspective(200px) rotateY(90deg) skewX(0deg);
		@include trans;
	}
	&.hide {
		display: none;
	}
}

@keyframes down {
    0% {
        background-position: 50% 50%;
    }
    50% {
        background-position: 50% 20%;
    }
    100% {
        background-position: 50% 50%;
    }
}

@media only screen and (max-width: 1199px) {
	.pageHeader {
		padding: 20px 0 30px;
	}
	.btnDown {
		bottom: 30px;
	}
	.content {
		&__inner {
			grid-row-start: 1;
		}
	}
}


@media only screen and (max-width: 991px) {
	.mainHeading {
		font-size: 28px;
	}
	.descrText {
		font-size: 15px;
	}
	.content {
		grid-template-rows: 25% 41% 33%;
	}
}

@media only screen and (max-width: 767px) {
	.content {
		grid-template-columns: 65% 35%;
	}
	.pageHeader {
		background-position: 85% 50%;
	}
}

@media only screen and (max-width: 575px) {
	.content {
		grid-template-columns: 80% 20%;
		&__inner {
			grid-row-start: 2;
		}
	}
	.btnDown {
		bottom: 10px;
		right: 10px;
	}
	.mainHeading {
		font-size: 20px;
		margin: 0 0 25px;
	}
	.descrText {
		font-size: 13px;
	}
}
