@import "variables";
.menu {
	@include trans;
	&.show {
		transform: translateX(0);
	}
	li {
		display: inline-block;
		margin: 0 10px 0;
		&:last-child {
			margin-right: 0;
		}
		a {
			position: relative;
			color: #fff;
			padding: 10px;
			&:hover,
			&.active {
				color: $main;
				&:before {
					transform: perspective(200px) rotateY(0deg)
				}
			}
			&:before {
				content: "";
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				border: 2px solid $main;
				transform: perspective(200px) rotateY(90deg) skewX(0deg);
				@include trans;
			}
		}
	}
}

.navigation {
	padding: 40px 0;
	position: absolute;
	z-index: 2;
	width: 100%;
	top: 0;
	background-color: rgba(0, 0, 0, .3);
	@include trans;
}
.fixed {
	position: fixed;
	padding: 20px 0;
	box-shadow: 0px 5px 5px 0px rgba(255,255,255,0.25);
	background-color: rgba(0, 0, 0, .5);
}

.menuToggle {
	display: block;
    position: relative;
    width: 36px;
    height: 36px;
    margin: 10px 0;
    overflow: hidden;
    float: right;
    background: transparent;
    cursor: pointer;
    &.active {
		.mMt {
			&.one {
				top: -50px;
				opacity: 0;
			}
			&.two {
				transform: rotate(45deg);
				top: 17px;
			}
			&.three {
				transform: rotate(-45deg);
				top: 17px;
			}
			&.four {
				top: 50px;
				opacity: 0;
			}
		}
	}
}



.mMt{
    width: 26px;
    height: 3px;
    background: $default;
    display: block;
    position: absolute;
    transition: all .3s ease-in-out;
    left: 5px;
    &.one {
	    top: 7px;
	}
	&.two {
	    top: 13px;
	}
	&.three {
	    top: 19px;
	}
	&.four {
	    top: 25px;
	}
}

.content {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.logo {
	color: $default;
}

.right {
	nav {
		display: flex;
		align-items: center;
	}
}

.socialWrap {
	width: 40px;
	height: 40px;
	margin: 0 30px;
	background-image: url("../img/style/svg/networks.svg");
	background-size: 40px;
	background-repeat: no-repeat;
	background-position: center right;
	display: flex;
	align-items: center;
	@include trans;
	&:hover {
		background-image: none;
		.socialList {
			transform: translateX(-70%);
			opacity: 1;
		}
	}
}

.socialList {
	min-width: 135px;
	opacity: 0;
	@include trans;
	li {
		display: inline-block;
		margin: 0 10px;
	}
}

.socialLnk {
	width: 25px;
	height: 25px;
	display: inline-block;
	@include trans;
	transform: scale(1);
	&:hover {
		transform: scale(1.5);
		opacity: 1;
	}
	ion-icon {
		font-size: 26px;
		color: $default;
	}
}


@media only screen and (max-width: 991px) {
	.navigation {
		padding: 30px 0;
		&.fixed {
			padding: 15px 0;
		}
	}
}

@media only screen and (max-width: 767px) {
	.menu {
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		min-width: 200px;
		background-color: rgba(0, 0, 0, .6);
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 120px 15px 60px;
		transform: translateX(100%);
		li {
			margin: 15px 0;
		}
	}
	.navigation {
		padding: 15px 0;
		&.fixed {
			padding: 5px 0;
		}
	}
	.socialWrap {
		display: none;
	}
}


@media only screen and (min-width: 768px) {
	.menuToggle {
		display: none;
	}
}

@media (pointer: coarse) {
	.socialWrap {
		display: none;
	}
}