@import "variables";

.pageTable {
	width: 100%;
	border-collapse: collapse;
	td {
		border: 2px solid $main;
		padding: 15px;
		line-height: 1.4em;
		&:first-child {
			width: 30%;
			min-width: 170px;
			font-family: "OpenSansBold";
		}
	}
}

@media only screen and (max-width: 767px) {
	.pageTable {
		td {
			padding: 10px;
			font-size: 14px;
		}
	}
}