@import "variables";

.experienceSection {
	background-color: #eceff1;
}

.contentWrap {
	display: flex;
	flex-direction: column;
	padding: 50px 0 40px;
}

.contentItm {
	display: flex;
	margin: 0 0 30px;
}

.left {
	width: 270px;
	position: relative;
	img {
		width: 100%;
		box-shadow: 0px 0px 16px 1px rgba(0,0,0,0.25);
	}
}

.right {
	width: calc(100% - 270px);
	padding: 0 0 0 40px;
}

.heading {
	font-size: 28px;
	margin: 0 0 30px;
}

.description {
	h6 {
		font-family: "OpenSansBold";
	}
	p {
		margin: 10px 0;
	}
}



@media only screen and (max-width: 767px) {
	.heading {
		font-size: 22px;
	}
	.left {
		width: 200px;
	}
	.right {
		width: calc(100% - 200px);
	}
	.contentWrap {
		padding: 10px 0 0px;
	}
}

@media only screen and (max-width: 575px) {
	.left {
		width: 160px;
	}
	.right {
		width: calc(100% - 160px);
		padding: 0 0 0 20px;
	}
	.heading {
		font-size: 18px;
		margin: 0 0 15px;
	}
	.description {
		h6,
		p {
			font-size: 14px;
		}
	}
}

@media only screen and (max-width: 480px) {
	.heading {
		position: absolute;
		top: 0;
	}
	.contentItm {
		position: relative;
		flex-direction: column;
		padding: 50px 50px 0;
	}
	.left {
		width: 100%;
		img {
			display: inline-block;
			max-width: 250px;
		}
	}
	.right {
		width: 100%;
		padding: 20px 0 0;
	}
}

@media only screen and (max-width: 350px) {
	.contentItm {
		padding: 50px 0 0;
	}
	.left {
		text-align: center;
	}
}