@import "variables";

footer {
	&.pageFooter {
		text-align: center;
	}
}

.top {
	padding: 20px 0;
	background-color: #f5f5f5;
}

.bottom {
	padding: 20px 0 20px;
	border-top: 1px solid $bdc;
}

.copywright {
	font-size: 12px;
}

.socialLinks {
	li {
		display: inline-block;
	}
}

.socialLnk {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30px;
	height: 30px;
	margin: 10px;
	background-size: cover;
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: -10px;
		right: -10px;
		bottom: -10px;
		left: -10px;
		border: 2px solid $main;
		transform: perspective(200px) rotateY(90deg);
		@include trans;
	}
	&:hover {
		&:before {
			transform: perspective(200px) rotateY(0deg);
		}
		ion-icon {
			color: $main;
		}
	}
	ion-icon {
		font-size: 40px;
		color: $txt;
		@include trans;
	}
}

.facebook {
	background-image: url("../img/style/svg/facebook.svg");
	&:hover {
		background-image: url("../img/style/svg/facebook-hover.svg");
	}
}
.vk {
	background-image: url("../img/style/svg/vk.svg");
	&:hover {
		background-image: url("../img/style/svg/vk-hover.svg");
	}
}

.phoneMail {
	margin: 15px 0 20px;
	li {
		display: inline-block;
		margin: 0 10px;
		a {
			color: #455a64;
			position: relative;
			&:before {
				content: "";
				position: absolute;
				display: block;
				width: 100%;
				height: 0px;
				background-color: $main;
				bottom: 0;
				@include trans;
			}
			&:hover {
				color: $main;
				&:before {
					bottom: -5px;
					height: 3px;
				}
			}
		}
	}
}


@media only screen and (max-width: 480px) {
	.phoneMail {
		li {
			margin: 5px;
		}
	}
}
