@import "normalize";
@import "variables";


html {
  overflow-x: hidden;
    max-width: 100vw !important;
}

body {
  margin: 0;
  padding: 0;
  color: $txt;
  // font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  //   "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  //   sans-serif;
  font-family: "OpenSansRegular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.2;
  font-size: 16px;
  &.addOverflow {
    overflow: hidden;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container,
.container-fluid {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
    margin-left: auto;
}

.container {
  max-width: 1390px;
}

.text-center {
  text-align: center !important;
}

.text-right {
  text-align: right !important;
}
.text-left {
  text-align: left !important;
}

a {
  color: $links;
  @include trans;
}

.heading {
    font-family: "OpenSansBold";
    position: relative;
    padding: 15px 0 20px;
    margin: 0 0 30px;
    &:before {
      content: "";
      position: absolute;
      width: 45px;
      height: 4px;
      background-color: $txt;
      display: block;
      bottom: 10px;
    }
}

h1, h2, h3, h4, h5, h6 {
  font-family: "OpenSansBold";
}

h6 {
  font-size: 16px;
  margin: 10px 0 6px;
}

h3.heading {
  font-size: 30px;
}

.pageSection {
  padding: 40px 0 60px;
}

//Buttons styles
.btn {
  display: inline-block;
  user-select: none;
  text-align: center;
  font-size: 14px;
  min-width: 210px;
  text-transform: uppercase;
  border: 2px solid;
  padding: 10px 15px;
  text-transform: uppercase;
  cursor: pointer;
  @include trans;
  &:hover {
    opacity: .8;
  }
}

.table-wrap {
  overflow-x: auto;
}

.btnDefault {
  @extend .btn;
  border-color: #fff;
  color: #fff;
}

.btnSuccess {
  @extend .btn;
  border-color: $main;
  color: $main;
  &:hover {
    background-color: $main;
    color: #fff;
  }
}

@font-face {
  font-family: "OpenSansRegular";
  src: local("OpenSansRegular.ttf"),
       url("./../fonts/OpenSans/OpenSansRegular/OpenSansRegular.ttf");
  font-style: normal;
}

@font-face {
  font-family: "OpenSansBold";
  src: local("OpenSansBold.ttf"),
       url("./../fonts/OpenSans/OpenSansBold/OpenSansBold.ttf");
  font-style: normal;
}

@font-face {
  font-family: "OpenSansSemiBold";
  src: local("OpenSansSemiBold.ttf"),
       url("./../fonts/OpenSans/OpenSansSemiBold/OpenSansSemiBold.ttf");
  font-style: normal;
}

@media only screen and (max-width: 992px) {
  .pageSection {
    padding: 30px 0 40px;
  }
  h3 {
    &.heading {
      font-size: 26px;
      &:before {
        width: 40px;
      }
    }
  }
}

@media only screen and (max-width: 575px) {
  .swiper-button-prev.workSwiperPrev,
  .swiper-button-next.workSwiperNext {
    display: none;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
  .swiper-button-prev.workSwiperPrev {
    left: -50px;
  }

  .swiper-button-next.workSwiperNext {
    right: -50px;
  }
  .swiper-container > .swiper-pagination-bullets.workPagination {
    bottom: -40px;
  }
}

@media only screen and (max-width: 480px) {
  .pageSection {
    padding: 15px 0 25px;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 970px;
  }
}


@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

